.navb {
    font-size: 20px;
    text-align: center;
    padding: 7px;
    width: 100%;
    color: antiquewhite;
    top: 0;
    background-color: transparent;
    transition: all .1s;
    border-bottom: 1px border rgb(29, 146, 29);
} 

.navb.filled {
    background-color: white;
    color:rgb(37, 36, 36);
    border-bottom: 5px solid rgba(255,196,0,255);

}
#navbar span.link-color{
    color: white;
    text-decoration: none;
}